import '../lib/tensorflowjs-worker-workaround';
import { loadModels, next as _next } from '../lib/nn';

let data = undefined;

export async function load() {
  const it = loadModels();

  while (true) {
    const val = await it.next();
    if (val.done === true) {
      postMessage({ type: 'progress', value: 1 });
      data = val.value;
      break;
    } else {
      postMessage({ type: 'progress', value: val.value });
    }
  }
}

export const next = (previousStrokeVectors => (
  nextImages,
  frames,
  additionalFrames,
  additionalFramesStep,
) =>
  _next(data, previousStrokeVectors, nextImages, frames, additionalFrames, additionalFramesStep))(
  [],
);

// needed for TypeScript to correctly intepret what workerize-loader exports
// eslint-disable-next-line no-native-reassign
export default Worker = () => ({ load, next, ready: Promise.resolve() });

addEventListener('message', function (e) {var ref = e.data;var type = ref.type;var method = ref.method;var id = ref.id;var params = ref.params;var f, p;if (type === 'RPC' && method) {if (f = __webpack_exports__[method]) {p = Promise.resolve().then(function () { return f.apply(__webpack_exports__, params); });} else {p = Promise.reject('No such method');}p.then(function (result) {postMessage({type: 'RPC',id: id,result: result});}).catch(function (e) {var error = {message: e};if (e.stack) {error.message = e.message;error.stack = e.stack;error.name = e.name;}postMessage({type: 'RPC',id: id,error: error});});}});postMessage({type: 'RPC',method: 'ready'});